const Icon = ({ name, style = {}, ...props }) => {
  const SvgIcon = icons[name];

  if (!SvgIcon) {
    console.error(`Icon "${name}" does not exist.`);
    return;
  }

  return React.cloneElement(SvgIcon, {
    ...props,
    style: {
      ...SvgIcon.props.style,
      ...style,
    },
  });
};

export default Icon;
